import { actionCreator } from '..';
import {
  atmData, card, cardPayload, sentCard
} from '../../types/card';
import {
  UPDATE_ATM_LIST_DATA,
  UPDATE_ATM_LIST_LOADING,
  UPDATE_CARDS_DATA,
  UPDATE_SENT_CARDS_DATA,
  UPDATE_CARDS_LIST_LOADING_STATE,
  UPDATE_SELECTED_CARD_DATA,
  UPDATE_CARD_PAYLOAD_DATA,
  UPDATE_REPLACE_CARD_ID
} from '../actionTypes/card';

/**
 * update card list data
 * @param {*} payload
 * @returns {Action}
 */
export const updateCards: actionCreator<{
  data: card[];
  total: number;
}> = (payload) => ({
  type: UPDATE_CARDS_DATA,
  payload,
});

/**
 * update sent card list data
 * @param {*} payload
 * @returns {Action}
 */
export const updateSentCards: actionCreator<{
  data: sentCard[];
  total: number;
}> = (payload) => ({
  type: UPDATE_SENT_CARDS_DATA,
  payload,
});

/**
 * update atm list
 * @param {*} payload
 * @returns {Action}
 */
export const updateCardListLoading: actionCreator<boolean> = (payload) => ({
  type: UPDATE_CARDS_LIST_LOADING_STATE,
  payload,
});

/**
 * update atm list
 * @param {*} payload
 * @returns {Action}
 */
export const updateAtmList: actionCreator<{
  data: atmData[];
  total: number;
}> = (payload) => ({
  type: UPDATE_ATM_LIST_DATA,
  payload,
});

/**
 * update atm list loading status
 * @param {*} payload
 * @returns {Action}
 */
export const updateAtmListLoading: actionCreator<boolean> = (payload) => ({
  type: UPDATE_ATM_LIST_LOADING,
  payload,
});

/**
 * Update current view card data
 * @param {card} payload
 * @returns {Action}
 */
export const updateSelectedCard: actionCreator<card> = (payload) => ({
  type: UPDATE_SELECTED_CARD_DATA,
  payload,
});

/**
 * Update card payload data
 * @param {card} payload
 * @returns {Action}
 */
export const updateCardPayload: actionCreator<cardPayload> = (payload) => ({
  type: UPDATE_CARD_PAYLOAD_DATA,
  payload,
});

/**
 * Update replace card id
 * @param {string} payload
 * @returns {Action}
 */
export const updateReplaceCardId: actionCreator<string | null> = (payload) => ({
  type: UPDATE_REPLACE_CARD_ID,
  payload,
});
