import React, { useCallback, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack, Switch
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import Icon from '../../../components/Icon';
import style from './AtmInfo.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { updateCard } from '../../../store/middlewares/card';
import { updateSelectedCard } from '../../../store/actionCreators/card';
import { apiErrorHandler } from '../../../utils/general';
import MiniBox from '../../../layout/MiniBox';
import { useDrawer } from '../../../hooks/drawer';

const AtmPin = React.lazy(() => import('../AtmPin/AtmPin'));
const AtmList = React.lazy(() => import('../AtmList/AtmList'));

const ATMInfo = () => {
  const { t } = useTranslation();
  const { open, close, goBack } = useDrawer();
  const dispatch = useAppAsyncDispatch();
  const cardData = useAppSelector((store) => store.card.selectedCard, shallowEqual);

  useEffect(() => {
    if (cardData === null) {
      close();
    }
  }, [cardData]);

  const togglrATMAccess = useCallback(() => {
    if (cardData) {
      dispatch(updateCard(cardData.id, {
        atmAccess: !cardData.atmAccess
      })).then((c) => {
        dispatch(updateSelectedCard(c));
      }).catch(apiErrorHandler);
    }
  }, [cardData]);

  return (
    <Box className="page">
      <MiniBox
        onBack={() => {
          goBack();
        }}
        heading={t('atm_info.title')}
      >
        <Box width="100%">
          <Box className={style.atmInfo__action_in_list}>
            <Text variant="regular" textAlign="left">{t('atm_info.atmAccess')}</Text>
            <Text color="#2EC193" pl="4px" variant="regular" textAlign="left">{cardData?.atmAccess ? t('atm_info.on') : t('atm_info.off', 'Off')}</Text>
            <Switch defaultChecked={cardData?.atmAccess} onChange={togglrATMAccess} ml="auto" variant="primary" id="send-by-email" />
          </Box>

          <HStack className={`${style.atmInfo__action_in_list} ${style.pointer}`} onClick={() => open(AtmPin)}>
            <Text variant="regular" mr="auto" textAlign="left">{t('atm_info.changePin')}</Text>
            <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
          </HStack>
          {cardData?.cardType === 'physical'
            ? (
              <HStack className={`${style.atmInfo__action_in_list} ${style.pointer}`} onClick={() => open(AtmList)}>
                <Text variant="regular" mr="auto" textAlign="left">{t('atm_info.findAtm')}</Text>
                <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
              </HStack>
            ) : null }
        </Box>

      </MiniBox>
    </Box>
  );
};

export default ATMInfo;
