import { AsyncAppAction, AsyncAppDispatch, RootState } from '..';
import {
  atmData, card, cardPayload, sentCard
} from '../../types/card';
import { loading } from '../../utils/general';
import http from '../../utils/http';
import {
  updateAtmList,
  updateAtmListLoading,
  updateCardListLoading,
  updateCards,
  updateSentCards,
  updateSelectedCard,
} from '../actionCreators/card';

/**
 * list all card
 * @param {string} accountId description
 * @param {number} limit description
 * @param {number} offset description
 * @returns {AsyncAppAction<card[]>}
 */
export const listAllCardByAccount = (
  accountId: string,
  limit = 25,
  offset = 0,
  query?: string
): AsyncAppAction<{
  data: card[];
  total: number;
}> => (dispatch: AsyncAppDispatch, getState: () => RootState) => new Promise((resolve, reject) => {
  dispatch(updateCardListLoading(true));
  let url = `/v1/card?accountId=${accountId}&limit=${limit}&offset=${offset}`;
  if (query) {
    url += `&query=${query}&last4=${query}`;
  }
  http
    .get(url)
    .then(({ data }) => {
      const state = getState().card;
      let d: {
            total: number;
            data: card[];
          } = {
            total: 0,
            data: [],
          };
      if (offset === 0) {
        d = data;
      } else {
        const cards = { ...state.cards };
        d = {
          data: [...cards.data, ...data.data],
          total: data.total,
        };
      }

      dispatch(updateCards(d));
      resolve(d);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      dispatch(updateCardListLoading(false));
    });
});

/**
 * create new card
 * @param {cardPayload} payload
 * @returns {AsyncAppAction<card>}
 */
export const createCard = (payload: cardPayload): AsyncAppAction<card> => (dispatch) => {
  loading(true);
  return new Promise((resolve, reject) => {
    http
      .post<card>('/v1/card', payload)
      .then(({ data }) => {
        dispatch(updateSelectedCard(data));
        resolve(data);
      })
      .catch((e) => {
        reject(e);
      })
      .finally(() => {
        loading(false);
      });
  });
};

/**
 * Activate card
 * @param {string} cardId
 * @param {{last4: string, expiryMonth: string, expiryYear: string}} payload
 * @returns {*}
 */
export const activate = (
  cardId: string,
  payload: {
    last4: string;
    expiryMonth: string;
    expiryYear: string;
  },
): AsyncAppAction<unknown> => (dispatch: AsyncAppDispatch, getState: () => RootState) => new Promise((resolve, reject) => {
  loading(true);
  http
    .patch(`/v1/card/${cardId}/activate`, payload)
    .then(({ data }) => {
      const { selectedAccount } = getState().account;
      if (selectedAccount) {
        setTimeout(() => {
          dispatch(listAllCardByAccount(selectedAccount.id));
        }, 0);
      }

      resolve(data);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      loading(false);
    });
});

