/**
 * Fetch near atm list
 * @param {{cardId: string, lat: string, long: string, limit: number, page: number}} param
 * @returns {*}
 */
export const findNearATM = ({
  cardId,
  lat,
  long,
  limit = 25,
  page = 1,
}: {
  cardId: string;
  lat: string;
  long: string;
  limit?: number;
  page?: number;
}): AsyncAppAction<{data: atmData[]; total: number}> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  dispatch(updateAtmListLoading(true));
  const offset = limit * (page - 1);
  http
    .get(
      `v1/card/atm?cardId=${cardId}&limit=${limit}&offset=${offset}&latitude=${lat}&longitude=${long}&radius=5`,
    )
    .then(({ data }) => {
      dispatch(updateAtmList(data));
      resolve(data);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      dispatch(updateAtmListLoading(false));
    });
});

