/**
 * list all sent cards
 * @param {string} accountId description
 * @param {number} limit description
 * @param {number} offset description
 * @returns {AsyncAppAction<card[]>}
 */
export const listAllSentCards = (
  accountId: string,
  limit = 25,
  offset = 0,
  query?: string
): AsyncAppAction<{
  data: sentCard[];
  total: number;
}> => (dispatch: AsyncAppDispatch, getState: () => RootState) => new Promise((resolve, reject) => {
  dispatch(updateCardListLoading(true));
  let url = `/v1/send/card?accountId=${accountId}&limit=${limit}&offset=${offset}`;
  if (query) {
    url += `&query=${query}`;
  }
  http
    .get(url)
    .then(({ data }) => {
      const state = getState().card;
      const cardData = data.data.filter((c:any) => c.card !== null);
      let d: {
            total: number;
            data: sentCard[];
          } = {
            total: 0,
            data: [],
          };
      if (offset === 0) {
        d = {
          data: cardData,
          total: cardData.length,
        };
      } else {
        const sentCards = { ...state.sentCards };

        d = {
          data: [...sentCards.data, ...cardData],
          total: cardData.length,
        };
      }
      dispatch(updateSentCards(d));
      resolve(d);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      dispatch(updateCardListLoading(false));
    });
});

